'use strict';
let bar = document.querySelector('.bar__legend');
let arrow = document.querySelector('.bar__arrow');

bar.addEventListener('click', function (e) {
	let target = e.target;
	if (target.tagName !== 'SPAN') {
		return;
	}
	switch (Number(target.dataset.level)) {
		case 0:
			arrow.style.left = 0;
			break;
		case 1:
			arrow.style.left = '140px';
			break;
		case 2:
			arrow.style.left = '365px';
			break;
		case 3:
			arrow.style.left = '751px';
			break;
	}
});

// Функция для получения координат элемента
function getCoords(elem) {
	let box = elem.getBoundingClientRect();

	return {
		top: box.top + pageYOffset,
		left: box.left + pageXOffset
	};
}

// Основная функция перемещения слайдера
function sliderMove(e) {
	e.preventDefault();

	let eventmove;
	let eventstop;

	// Определяем как пользователь пытается перемещать слайдер (мышкой или touch устройством)
	switch (e.type) {
		case 'mousedown':
			eventmove = 'mousemove';
			eventstop = 'mouseup';
			break;
		case 'touchstart':
			eventmove = 'touchmove';
			eventstop = 'touchend';
			break;
	}

	// Координаты стрелки
	let arrowCoords = getCoords(arrow);
	let shiftX = e.pageX - arrowCoords.left;

	// Координаты бара
	let barCoords = getCoords(bar);

	// Функция перемещения стрелки
	let move = function (event) {
		let newLeft = event.pageX - shiftX - barCoords.left;

		if (newLeft < 0) {
			newLeft = 0;
		}

		let rightEdge = bar.offsetWidth - arrow.offsetWidth;

		if (newLeft > rightEdge) {
			newLeft = rightEdge;
		}

		arrow.style.transition = 'left 0s';
		arrow.style.left = newLeft + 'px';

		return false;
	};

	// Функция окончания перемещения
	let stop = function () {
		// Удаляем события при отпускании пользователем мыши или touch устройства
		document.removeEventListener(eventmove, move);
		document.removeEventListener(eventstop, stop);
		arrow.style.transition = '';

		return false;
	};

	document.addEventListener(eventmove, move);
	document.addEventListener(eventstop, stop);
}

arrow.addEventListener('mousedown', sliderMove);
arrow.addEventListener('touchstart', sliderMove);



